.map__header {
  position: relative;
  z-index: 2;
  margin-top: -30px;
  background-color: $basic-yellow;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    margin-top: -60px;
    border-bottom-left-radius: 56px;
    border-bottom-right-radius: 56px;
  }

  @media (min-width: $desktop-width) {
    border-bottom-left-radius: 60px;
  }
}

.map .container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-top: 61px;
  padding-bottom: 35px;

  @media (min-width: $tablet-width) {
    padding-top: 108px;
    padding-bottom: 84px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    padding-top: 114px;
    padding-bottom: 57px;
  }
}

.map__adress {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 16px;
  align-items: center;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
  text-transform: uppercase;
  text-decoration: none;
  color: $basic-blue--dark;

  @media (min-width: $tablet-width) {
    column-gap: 26px;
    margin-right: 95px;
    font-size: 28px;
    line-height: 28px;
  }

  @media (min-width: $desktop-width) {
    column-gap: 31px;
    margin: 0 148px;
  }

  &::before {
    content: "";
    display: block;
    width: 45px;
    height: 45px;
    background-color: $special-blue--buble;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 21px 21px;
    background-image: url("../img/location.svg");
    border-radius: 50%;

    @media (min-width: $tablet-width) {
      width: 68px;
      height: 68px;
      background-size: 32px 32px;
    }

    @media (min-width: $desktop-width) {
      width: 90px;
      height: 90px;
      margin-top: 1px;
      margin-bottom: -1px;
      background-size: 42px 42px;
    }
  }
}

.map__link {
  margin-top: 33px;

  @media (min-width: $tablet-width) {
    margin-top: 44px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 1px;
  }

  &::after {
    @media (min-width: $tablet-width) {
      top: 32px;
      right: 32px;
    }
  }
}

.map__wrapper {
  position: relative;
  z-index: 1;
  height: 424px;
  margin-left: auto;
  margin-right: auto;
  margin-top: -65px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    height: 581px;
    margin-top: -50px;
  }

  @media (min-width: $desktop-width) {
    height: 584px;
    margin-top: -93px;
  }
}

.map__img {
  display: block;
  width: 100%;
  height: auto;
}
