.about {
  position: relative;
  z-index: 7;
  background-color: $special-blue--dark;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    background-color: $special-blue--bg;
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -60px;
    background-color: $special-blue--dark;
    border-bottom-left-radius: 56px;
    border-bottom-right-radius: 56px;
  }
}

.about .container {
  padding-top: 31px;
  padding-bottom: 83px;
  background-repeat: no-repeat;
  background-size: 292px 353px, 132px 132px;
  background-position-x: 20px, -23px;
  background-image:
    url("../img/about-mobile@1x.png"),
    url("../img/rectangle-about-mobile.svg");

  @media (min-resolution: 2dppx) {
    background-image:
      url("../img/about-mobile@2x.png"),
      url("../img/rectangle-about-mobile.svg");
  }

  @media (min-width: $tablet-width) {
    padding-top: 37px;
    padding-bottom: 89px;
    background-size: 376px 548px, 688px 212px;
    background-position: 384px 48px, 40px 0;
    background-image:
      url("../img/about-tablet@1x.png"),
      url("../img/rectangle-about-tablet.svg");

    @media (min-resolution: 2dppx) {
      background-image:
        url("../img/about-tablet@2x.png"),
        url("../img/rectangle-about-tablet.svg");
    }
  }

  @media (min-width: $desktop-width) {
    padding-top: 188px;
    padding-bottom: 163px;
    background-size: 630px 665px, 212px 212px;
    background-position: 750px 145px, 60px 130px;
    background-image:
      url("../img/about-desktop@1x.png"),
      url("../img/rectangle-about-mobile.svg");

    @media (min-resolution: 2dppx) {
      background-image:
        url("../img/about-desktop@2x.png"),
        url("../img/rectangle-about-mobile.svg");
    }
  }
}

.about__button {
  @media (max-width: $mobile-width-only) {
    display: none;
  }

  @media (min-width: $tablet-width) {
    width: 276px;
    margin-top: 54px;
    margin-left: 34px;

    &::after {
      top: 34px;
      right: 36px;
    }
  }

  @media (min-width: $desktop-width) {
    width: 274px;
    margin-top: 87px;
    margin-left: 71px;
  }
}

.about__desc {
  margin-top: 20px;
  color: $basic-white;

  @media (min-width: $tablet-width) {
    max-width: 345px;
    margin-top: 42px;
  }

  @media (min-width: $desktop-width) {
    max-width: 690px;
    margin-top: 59px;
    line-height: 34px;
  }
}

.about__desc.desc--important {
  margin-top: 19px;

  @media (min-width: $tablet-width) {
    margin-top: 31px;
    line-height: 25px;
  }

  @media (min-width: $desktop-width) {
    max-width: 495px;
    margin-top: 38px;
    line-height: 24px;
  }
}

.about__crutch {
  display: none;

  @media (min-width: $tablet-width) and (max-width: $desktop-width - 1) {
    display: block;
  }
}
