.level {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-style: normal;
  background-repeat: no-repeat;
  background-size: cover;

  &--25 {
    background-image: url("../img/level-25.svg");
  }

  &--50 {
    background-image: url("../img/level-50.svg");
  }

  &--80 {
    background-image: url("../img/level-80.svg");
  }

  &--99 {
    background-image: url("../img/level-99.svg");
  }
}

.level__value {
  position: relative;
  margin-top: 13px;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -2px;
    display: block;
    width: 30px;
    height: 1px;
    background-color: rgba($basic-white, 0.3);
  }
}

.level__text {
  margin-top: 1px;
  margin-left: -1px;
  font-size: 14px;
  line-height: 14px;
}
